#darkmode{
    display: flex;
    justify-content: center;
}

#darkmode .dark {
    background-color: #292c35;
}
#darkmode .checkbox {
    opacity: 0;
    position: absolute;
}

#darkmode .label {
    width: 39px;
    height: 14px;
    background-color:mediumseagreen;
    display: flex;
    border-radius:50px;
    align-items: center;
    justify-content: space-between;
    padding: 5px;
    position: relative;
    transform: scale(1.5);
    cursor: pointer;
}

#darkmode .ball {
    width: 21px;
    height: 20px;
    background-color: mediumaquamarine;
    position: absolute;
    top: 2px;
    left: 2px;
    border-radius: 50%;
    transition: transform 0.2s linear;
}

#darkmode .checkbox:checked + .label .ball{
    transform: translateX(24px);
}